<template>
  <div>
    <div class="navigator">
      <div class="text" @click="toBack">返回</div>
      <div class="text">我的</div>
      <div class="text" style="visibility: hidden">添加</div>
    </div>
    <form>
      <label class="header">
        <label>账号</label>
        <label class="sub">{{name}}</label>
      </label>
      <label class="header" @click="toChangePwd">
        <label>修改密码</label>
        <label class="sub">点击修改密码</label>
      </label>
      <label class="subView" v-show="isChange">
        <label class="input">
          证件号:
          <input>
        </label>
        <label class="input">
          新密码:
          <input>
        </label>
        <label class="input">
          确认密码:
          <input>
        </label>
      </label>
    </form>
    <button @click="toLogout">{{isChange ? '确认修改': '退出登录'}}</button>
  </div>
</template>

<script>
export default {
  name: 'MyViewController',
  data () {
    return {
      isChange: false,
      name: ''
    }
  },
  created () {
    this.netUserInfo()
  },
  methods: {
    netUserInfo () {
      let param = {}
      this.$request('user/info.do', param)
        .then(rsp => {
          const data = rsp.data

          console.log(data)

          if (data.state === this.$state.success) {
            this.name = data.data.name
          }
        })
    },
    toBack () {
      this.$router.back()
    },
    toLogout () {
      this.$router.push('/')
    },
    toChangePwd () {
      this.isChange = !this.isChange
    }
  }
}
</script>

<style scoped>
  input {
    margin-left: 10px;
    margin-right: 10px;
    height: 38px;
    width: 400px;
    border-width: 1px;
    border-radius: 5px;
    font-size: 30px;
  }
  form {
    margin-top: 10px;
  }
  button {
    margin-top: 40px;
    width: 300px;
    height: 48px;
    font-size: 30px;
    color: white;
    background-color: #0088fb;
    border-radius: 10px;
  }
  div.navigator {
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: whitesmoke;
    width: auto;
    height: 60px;
  }
  div.text {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    color: black;
    padding-right: 10px;
    padding-left: 10px;
  }
  label.header {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: flex-start;
    border-bottom-style: solid;
    border-bottom-color: #f5f5f5;
    border-bottom-width: 1px;
  }
  label.input {
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  label.sub {
    margin-left: 10px;
    font-size: 20px;
    color: grey;
  }
  label.subView {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: inherit;
  }
</style>



