import Vue from 'vue'
import Router from 'vue-router'
import LoginViewController from '../components/LoginViewController'
import DeviceListViewController from '../components/DeviceListViewController'
import MyViewController from '../components/MyViewController'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: LoginViewController
    },
    {
      path: '/deviceList',
      name: 'deviceList',
      component: DeviceListViewController
    },
    {
      path: '/my',
      name: 'my',
      component: MyViewController
    }
  ]
})



