<template>
  <div>
    <div class="navigator">
      <div class="text" @click="toMy">我的</div>
      <div class="text">集中监控</div>
      <div class="text">添加</div>
    </div>
    <div style="display: flex; flex-direction: row; flex-wrap: wrap; ">
      <div v-for="(item, i) in dataArr" :key="i" class="content">
        <div class="remark">
          <div class="remark_content" :style="item.online ? {backgroundColor: '#0088fb'}: {backgroundColor: '#c9c9c9'}"></div>
          <div class="title">{{item.remark}}</div>
        </div>
        <div class="subTitle">{{item.mid}}</div>
        <div class="content_div">
          <div style="display: flex; flex-direction: row">
            <div class="content_title">温度: </div>
            <div class="content_t" style="font-weight: bold; color: red">{{realData.hasOwnProperty(item.mid) ? realData[item.mid].dryAim : 0}}℃</div>
          </div>
          <div style="display: flex; flex-direction: row">
            <div class="content_title">湿度: </div>
            <div class="content_t" style="font-weight: bold; color: #0088fb">{{realData.hasOwnProperty(item.mid) ? realData[item.mid].wetAim : 0}}℃</div>
          </div>
        </div>
        <div class="subTitle">{{realData.hasOwnProperty(item.mid) ? realData[item.mid].alarmStatus.join(',') : '无'}}</div>
      </div>
    </div>
  </div>

</template>

<script>

import Vue from 'vue'

export default {
  name: 'DeviceListViewController',
  data () {
    return {
      title: '',
      dataArr: [],
      realData: {}
    }
  },
  created () {
    this.login()
  },
  methods: {
    login () {
      this.$request('device/list.do', {})
        .then(rsp => {
          this.dataArr = []
          let midArr = []
          let datas = rsp.data.data
          for (let i = 0, len = datas.length; i < len; i++) {
            const mid = datas[i]
            this.dataArr.push(mid)
            midArr.push(mid.mid)
          }
          this.realTime(midArr)
        })
    },
    realTime (midArr) {
      for (let i = 0, l = midArr.length; i < l; i++) {
        let mid = midArr[i]
        this.$request('device/realTime.do', {mid: mid})
          .then(rsp => {
            Vue.set(this.realData, mid, rsp.data.data.data)
            console.log(JSON.stringify(this.realData))
          })
      }
    },
    toMy () {
      this.$router.push('/my')
    }
  }
}
</script>

<style scoped>
  div.title {
    overflow: hidden;
    font-size: 25px;
    width: 130px;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  div.navigator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: whitesmoke;
    width: 100%;
    height: 60px;
  }
  div.content {
    padding: 15px;
    margin: 5px;
    max-width: 152px;
    border-width: 1px;
    border-radius: 10px;
    background-color: aliceblue;
    align-content: center;
  }
  div.content_div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  div.content_title {
    display: flex;
    font-size: 25px;
  }
  div.remark {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  div.remark_content {
    display: flex;
    width: 14px;
    height: 14px;
    border-radius: 7px;
  }
  div.text {
    display: flex;
    align-items: center;
    color: black;
    padding-right: 10px;
    padding-left: 10px;
  }
  div.subTitle {
    display: flex;
    font-size: 18px;
    justify-content: center;
  }
</style>



