<template>
  <div>
    <div class="navigator">
      <div class="text" style="visibility: hidden">返回</div>
      <div class="text">登录</div>
      <div class="text" @click="toRegister">注册</div>
    </div>
    <img src="../assets/logo.png" alt="">
    <form class="div">
      <label>
        用户名：
        <input type="text" ref="name">
      </label><br>
      <label>
        密<a style="padding-left: 30px">码：</a>
        <input type="password" ref="password">
      </label><br>
      <label v-show="isCard">
        证件号：
        <input type="password" ref="card">
      </label>
    </form>
    <button type="button" @click="toLogin">登录</button>
  </div>
</template>

<script>
import md5 from 'js-md5'

export default {
  name: 'LoginViewController',
  data () {
    return {
      isCard: false
    }
  },
  methods: {
    toLogin () {
      const name = this.$refs.name.value
      if (name === undefined || name.length === 0) {
        alert('请输入用户名！')
        return
      }
      let password = this.$refs.password.value
      if (password === undefined || password.length === 0) {
        alert('请输入密码！')
        return
      }
      password = md5(password)

      let param = {}
      let card = this.$refs.card.value
      if (this.isCard && (card === undefined || card.length === 0)) {
        alert('请输入证件号')
        return
      } else if (card !== undefined && card.length !== 0) {
        param.card = md5(card)
      }
      param.name = name
      param.password = password

      this.$request('user/login.do', param)
        .then(rsp => {
          const data = rsp.data

          if (data.state === this.$state.loginCard) {
            this.isCard = true
            alert(data.msg)
          } else {
            this.isCard = false
            this.$refs.card.value = undefined

            if (data.state === this.$state.success) {
              this.$setToken(data.data.token)
              this.$router.push('/deviceList')
            } else {
              alert(data.msg)
            }
          }
        })
    },
    toRegister () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
  input {
    height: 30px;
    min-width: 200px;
    font-size: 30px
  }
  form.div {
    display: flex;
    flex-direction: column;
    align-items: center
  }
  label {
    display: flex;
    align-items: center
  }
  button {
    /*display: flex;*/
    margin-top: 40px;
    width: 400px;
    height: 50px;
    background-color: #0088fb;
    color: white;
    font-size: 30px;
    border-radius: 10px;
  }
  div.navigator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: whitesmoke;
    width: 100%;
    height: 60px;
  }
  div.text {
    display: flex;
    align-items: center;
    color: black;
    padding-right: 10px;
    padding-left: 10px;
  }
</style>



